<router-link class="rounded overflow-hidden bg-white shadow d-block position-relative" :to="`/hotels/${hotel.HotelID}`" :title="hotel.HotelName"><img class="position-absolute link" src="@/assets/icons/link_icon.svg" alt="link icon"><img class="object-cover w-100" v-if="'PictureUrl1' in hotel.Picture" :src="hotel.Picture.PictureUrl1" :alt="hotel.HotelName" height="163"><img class="object-cover w-100" v-else src="@/assets/images/illustration.png" alt="該資料沒有圖片" title="該資料沒有圖片" height="163"><div class="px-3 py-2"><h4 class="text-dark text-truncate">{{ hotel.HotelName }}</h4><div class="vstack"><div class="phone mb-2 text-truncate" :title="phoneNumber">{{ phoneNumber }}</div><div class="location me-3 text-truncate" :title="hotel.Address">{{ hotel.Address }}</div></div></div></router-link>